import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { Context, SongsContext } from "./context";
import {
  Typography,
  Grid,
  ListItem,
  Paper,
  Drawer,
  Slide,
  Backdrop,
  IconButton,
  ClickAwayListener,
  Divider,
  Button,
  Dialog,
  DialogContent,
  List,
  ListItemText,
} from "@material-ui/core";
import Playlist from "./playlist";
import "./css/wrapper.css";
import Home from "./home";
import MenuIcon from "@material-ui/icons/Menu";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import MusicNoteIcon from "@material-ui/icons/MusicNote";
import FavoriteIcon from "@material-ui/icons/Favorite";
import AlbumIcon from "@material-ui/icons/Album";
import InfoIcon from "@material-ui/icons/Info";
import Favourite from "./favourites";
import KeyboardArrowLeftIcon from "@material-ui/icons/KeyboardArrowLeft";

class Wrapper extends Component {
  state = {
    tab: 0,
    loading: true,
    DrawerOpen: false,
    currentSongOpen: false,
    DialogOpen: false,
  };

  constructor(props) {
    super(props);
    this.HomeRef = new React.createRef();
  }

  handleCurrentSongSlide = () => {
    this.setState({ currentSongOpen: !this.state.currentSongOpen });
  };

  //handle the info  dialog
  handleDialog = () => {
    this.setState({ DialogOpen: !this.state.DialogOpen });
  };
  render() {
    return (
      <Context>
        <SongsContext.Consumer>
          {(object) => {
            return (
              <Router basename={window.location.pathname || ""}>
                <Paper className="wrapper-paper" elevation={2}>
                  <Grid
                    container
                    justify="center"
                    alignItems="center"
                    className="title-grid"
                  >
                    <Grid
                      item
                      xs
                      style={{
                        textAlign: "center",
                      }}
                    >
                      <ClickAwayListener
                        onClickAway={() => {
                          this.setState({ DrawerOpen: false });
                        }}
                      >
                        {this.state.currentSongOpen ? (
                          <KeyboardArrowDownIcon
                            size="small"
                            onClick={this.handleCurrentSongSlide}
                            style={{ color: "white", cursor: "pointer" }}
                          />
                        ) : (
                          <MenuIcon
                            size="small"
                            style={{ color: "white", cursor: "pointer" }}
                            onClick={() => {
                              this.setState({ DrawerOpen: true });
                            }}
                          />
                        )}
                      </ClickAwayListener>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography align="center" variant="h6" className="title">
                        {this.state.currentSongOpen ? "Now Playing" : "Music"}{" "}
                      </Typography>
                    </Grid>
                    <Grid item xs>
                      {this.state.currentSongOpen && (
                        <Button onClick={this.handleDialog} disableRipple>
                          <InfoIcon size="small" style={{ color: "white" }} />
                        </Button>
                      )}
                    </Grid>
                  </Grid>
                  <Route path="/" exact>
                    <div className="play-list">
                      <Playlist
                        checkforlike={(index) => {
                          this.HomeRef.current.checkForLike("random", index);
                        }}
                        resetSong={() => {
                          this.HomeRef.current.resetSong();
                        }}
                      />
                    </div>
                  </Route>
                  <Route path="/fav" exact>
                    <div className="play-list">
                      <Favourite
                        checkforlike={(index) => {
                          this.HomeRef.current.checkForLike("random", index);
                        }}
                        resetSong={() => {
                          this.HomeRef.current.resetSong();
                        }}
                      />
                    </div>
                  </Route>
                  <Paper className="music-player-bottom" elevation={4}>
                    <ListItem button onClick={this.handleCurrentSongSlide}>
                      <Grid container spacing={2} justify="space-between">
                        <Grid item>
                          <img src={object.songs[object.activeSong].img} />
                        </Grid>
                        <Grid item xs container direction="column">
                          <Grid item>
                            <Typography
                              variant="body2"
                              style={{
                                fontWeight: "bold",
                                textTransform: "capitalize",
                              }}
                            >
                              {object.songs[object.activeSong].song}
                            </Typography>
                          </Grid>
                          <Grid item>
                            <Typography
                              variant="subtitle2"
                              style={{ overflowX: "hidden" }}
                            >
                              {object.songs[object.activeSong].infoObj.artist}
                            </Typography>
                          </Grid>
                        </Grid>
                        <Grid item style={{ paddingRight: "10px" }}>
                          <div className={object.isPlay ? "anim-div" : null}>
                            <div className="one"></div>
                            <div className="two"></div>
                            <div className="three"></div>
                          </div>
                        </Grid>
                      </Grid>
                    </ListItem>
                  </Paper>
                  <Slide
                    direction="right"
                    in={this.state.DrawerOpen}
                    style={{
                      position: "absolute",
                      top: 0,
                      left: 0,
                      width: "150px",
                      height: "100%",
                    }}
                    timeout={{
                      enter: 600,
                      exit: 1000,
                    }}
                  >
                    <div style={{ width: "200px" }} className="slide">
                      <Typography
                        gutterBottom
                        align="right"
                        style={{ paddingRight: "10px" }}
                      >
                        <KeyboardArrowLeftIcon
                          style={{ cursor: "pointer" }}
                          onClick={() => {
                            this.setState({ DrawerOpen: false });
                          }}
                        />
                      </Typography>
                      <List>
                        <ListItem button component={Link} to="/">
                          <ListItemText>
                            <Typography variant="body1" align="left">
                              <MusicNoteIcon size="small" /> All songs
                            </Typography>
                          </ListItemText>
                        </ListItem>
                        <Divider />
                        <ListItem button component={Link} to="/fav">
                          <ListItemText>
                            <Typography variant="body1" align="left">
                              <FavoriteIcon size="small" /> Favourites
                            </Typography>
                          </ListItemText>
                        </ListItem>
                      </List>
                    </div>
                  </Slide>

                  <Slide
                    in={this.state.currentSongOpen}
                    direction="up"
                    style={{
                      position: "absolute",
                      bottom: 0,
                      left: 0,
                      width: "100%",
                      height: "91.5%",
                      zIndex: 100000,
                    }}
                    timeout={{
                      enter: 600,
                      exit: 400,
                    }}
                  >
                    <Paper>
                      <Home ref={this.HomeRef} />
                    </Paper>
                  </Slide>
                  <Dialog
                    open={this.state.DialogOpen}
                    onClose={this.handleDialog}
                  >
                    <DialogContent style={{ padding: 0 }}>
                      <List>
                        <ListItem>
                          <ListItemText>
                            <Typography variant="body1">
                              Title :{" "}
                              {object.songs[object.activeSong].infoObj.title}
                            </Typography>
                          </ListItemText>
                        </ListItem>
                        <ListItem>
                          <ListItemText>
                            <Typography variant="body1">
                              Album :{" "}
                              {object.songs[object.activeSong].infoObj.album}
                            </Typography>
                          </ListItemText>
                        </ListItem>
                        <ListItem>
                          <ListItemText>
                            <Typography variant="body1">
                              Genre :{" "}
                              {object.songs[object.activeSong].infoObj.genre}
                            </Typography>
                          </ListItemText>
                        </ListItem>
                        <ListItem>
                          <ListItemText>
                            <Typography variant="body1">
                              Artist :{" "}
                              {object.songs[object.activeSong].infoObj.artist}
                            </Typography>
                          </ListItemText>
                        </ListItem>
                        <ListItem>
                          <ListItemText>
                            <Typography variant="body1">
                              Year released :{" "}
                              {object.songs[object.activeSong].infoObj.year}
                            </Typography>
                          </ListItemText>
                        </ListItem>
                      </List>
                    </DialogContent>
                  </Dialog>
                </Paper>
              </Router>
            );
          }}
        </SongsContext.Consumer>
      </Context>
    );
  }
}

export default Wrapper;
