import React, { Component, createContext } from "react";

export const SongsContext = createContext();

export class Context extends Component {
  state = {
    list: [
      "inkaedo",
      "bharat",
      "preminche",
      "sanamre",
      "galat",
      "highrated",
      "bomdiggy",
      "pachtaoge",
      "sorry",
      "yaanji",
    ],
    songs: [
      {
        img: null,
        infoObj: {
          title: "",
          artist: "sai",
          album: "",
          genre: "",
          year: 2020,
        },
        song: "inkaedo",
      },
    ],
    i: 0,
    selected: {
      img: null,
      infoObj: {
        title: "",
        artist: "sai",
        album: "",
        genre: "",
        year: 2020,
      },
      song: "inkaedo",
    },

    activeSong: 0,
    isPlay: false,
  };
  componentDidMount = () => {
    this.setDetails();
  };
  // display the animation in wrapper only when a song is playing
  handlePlayPause = () => {
    this.setState({ isPlay: !this.state.isPlay });
  };

  //create the songs list
  setDetails = () => {
    var jsmediatags = require("jsmediatags");
    jsmediatags.read(
      `https://saiteja623.github.io${require(`./music/${
        this.state.list[this.state.i]
      }.mp3`)}`,
      {
        onSuccess: (e) => {
          var image = e.tags.picture;
          var str = "";
          for (var j = 0; j < image.data.length; j++) {
            str += String.fromCharCode(image.data[j]);
          }
          var base64 = "data:" + image.format + ";base64," + window.btoa(str);
          var o = {
            img: base64,
            infoObj: e.tags,
            song: this.state.list[this.state.i],
          };
          console.log(o);
          if (this.state.i == 0) {
            var songs = [];
            songs.push(o);
            this.setState({ songs, selected: o });
          } else {
            var songs = this.state.songs;
            songs.push(o);
            this.setState({ songs });
          }
          this.setState({ i: this.state.i + 1 });
          if (this.state.i !== 10) {
            this.setDetails();
          }
        },
      }
    );
  };

  //play the next by increasing the index
  playNext = () => {
    var activeSong = this.state.activeSong;
    if (activeSong === 9) {
      activeSong = 0;
    } else {
      activeSong = activeSong + 1;
    }
    this.setState({ activeSong });
    this.setPlayTrue();
  };

  //play the prev song by decreasing the index
  playPrev = () => {
    var activeSong = this.state.activeSong;
    if (activeSong === 0) {
      activeSong = 9;
    } else {
      activeSong = activeSong - 1;
    }
    this.setState({ activeSong });
    this.setPlayTrue();
  };
  setSong = (index) => {
    this.setState({ activeSong: index });
  };

  //set play to only to true

  setPlayTrue = () => {
    this.setState({ isPlay: true });
  };

  render() {
    return (
      <SongsContext.Provider
        value={{
          songs: this.state.songs,
          list: this.state.list,
          setSong: this.setSong,
          activeSong: this.state.activeSong,
          playPrev: this.playPrev,
          playNext: this.playNext,
          handlePlayPause: this.handlePlayPause,
          isPlay: this.state.isPlay,
          setPlayTrue: this.setPlayTrue,
        }}
      >
        {this.props.children}
      </SongsContext.Provider>
    );
  }
}
