import React, { Component } from "react";
import { SongsContext } from "./context";
import {
  List,
  ListItem,
  ListItemText,
  Tab,
  Tabs,
  Grid,
  Paper,
  Typography,
  Divider,
  AppBar,
} from "@material-ui/core";
import FlipMove from "react-flip-move";
import withRouter from "react-router-dom";
import "./css/home.css";
import SwipabeleViews from "react-swipeable-views";

class Playlist extends Component {
  state = {
    tabValue: 0,
  };
  //handles the tab changed
  handleTabs = (e, newValue) => {
    this.setState({ tabValue: newValue });
  };
  render() {
    return (
      <SongsContext.Consumer>
        {(object) => {
          return (
            <>
              <AppBar position="sticky" className="appbar">
                <Tabs
                  value={this.state.tabValue}
                  onChange={this.handleTabs}
                  variant="scrollable"
                  scrollButtons="off"
                  style={{ padding: 0 }}
                  indicatorColor="primary"
                >
                  <Tab
                    label="All songs"
                    style={{
                      fontSize: "0.72rem",
                      minWidth: "100px",
                    }}
                    wrapped
                    fullWidth
                    disableRipple
                  />
                  <Tab
                    label="Telugu"
                    style={{
                      fontSize: "0.72rem",
                      minWidth: "100px",
                    }}
                    disableRipple
                  />
                  <Tab
                    label="Hindi"
                    style={{ fontSize: "0.72rem", minWidth: "100px" }}
                    disableRipple
                  />
                  <Tab
                    label="English"
                    style={{ fontSize: "0.72rem", minWidth: "100px" }}
                    disableRipple
                  />
                  <Tab
                    label="Other"
                    style={{ fontSize: "0.72rem", minWidth: "100px" }}
                    disableRipple
                  />
                </Tabs>
              </AppBar>
              <SwipabeleViews index={this.state.tabValue}>
                <List>
                  <FlipMove
                    appearAnimation="fade"
                    enterAnimation="fade"
                    staggerDelayBy={1000}
                  >
                    {object.songs.map((e, index) => (
                      <>
                        <ListItem
                          button
                          onClick={() => {
                            object.setSong(index);
                            this.props.checkforlike(index);
                            this.props.resetSong();
                            object.setPlayTrue();
                          }}
                        >
                          <Grid container spacing={2} justify="space-between">
                            <Grid item>
                              <img src={e.img} />
                            </Grid>
                            <Grid
                              item
                              xs
                              container
                              direction="column"
                              style={
                                object.activeSong == index
                                  ? { color: "#ff3b57" }
                                  : null
                              }
                            >
                              <Grid item>
                                <Typography
                                  variant="body2"
                                  style={{
                                    fontWeight: "bold",
                                    textTransform: "capitalize",
                                  }}
                                >
                                  {e.song}
                                </Typography>
                              </Grid>
                              <Grid item>
                                <Typography variant="subtitle2">
                                  {e.infoObj.artist}
                                </Typography>
                              </Grid>
                            </Grid>
                          </Grid>
                        </ListItem>
                        <Divider />
                      </>
                    ))}
                  </FlipMove>
                </List>

                <div>
                  {object.songs.map((e, index) => {
                    if (index >= 0 && index < 3) {
                      return (
                        <>
                          <ListItem
                            button
                            onClick={() => {
                              object.setSong(index);
                              this.props.checkforlike(index);
                              this.props.resetSong();
                              object.setPlayTrue();
                            }}
                          >
                            <Grid container spacing={2} justify="space-between">
                              <Grid item>
                                <img src={e.img} />
                              </Grid>
                              <Grid
                                item
                                xs
                                container
                                direction="column"
                                style={
                                  object.activeSong == index
                                    ? { color: "#ff3b57" }
                                    : null
                                }
                              >
                                <Grid item>
                                  <Typography
                                    variant="body2"
                                    style={{
                                      fontWeight: "bold",
                                      textTransform: "capitalize",
                                    }}
                                  >
                                    {e.song}
                                  </Typography>
                                </Grid>
                                <Grid item>
                                  <Typography variant="subtitle2">
                                    {e.infoObj.artist}
                                  </Typography>
                                </Grid>
                              </Grid>
                            </Grid>
                          </ListItem>
                          <Divider />
                        </>
                      );
                    }
                  })}
                </div>
                <div>
                  {object.songs.map((e, index) => {
                    if (index >= 3 && index < 8) {
                      return (
                        <>
                          <ListItem
                            button
                            onClick={() => {
                              object.setSong(index);
                              this.props.checkforlike(index);
                              this.props.resetSong();
                              object.setPlayTrue();
                            }}
                          >
                            <Grid container spacing={2} justify="space-between">
                              <Grid item>
                                <img src={e.img} />
                              </Grid>
                              <Grid
                                item
                                xs
                                container
                                direction="column"
                                style={
                                  object.activeSong == index
                                    ? { color: "#ff3b57" }
                                    : null
                                }
                              >
                                <Grid item>
                                  <Typography
                                    variant="body2"
                                    style={{
                                      fontWeight: "bold",
                                      textTransform: "capitalize",
                                    }}
                                  >
                                    {e.song}
                                  </Typography>
                                </Grid>
                                <Grid item>
                                  <Typography variant="subtitle2">
                                    {e.infoObj.artist}
                                  </Typography>
                                </Grid>
                              </Grid>
                            </Grid>
                          </ListItem>
                          <Divider />
                        </>
                      );
                    }
                  })}
                </div>
                <div>
                  {object.songs.map((e, index) => {
                    if (index === 8) {
                      return (
                        <>
                          <ListItem
                            button
                            onClick={() => {
                              object.setSong(index);
                              this.props.checkforlike(index);
                              this.props.resetSong();
                              object.setPlayTrue();
                            }}
                          >
                            <Grid container spacing={2} justify="space-between">
                              <Grid item>
                                <img src={e.img} />
                              </Grid>
                              <Grid
                                item
                                xs
                                container
                                direction="column"
                                style={
                                  object.activeSong == index
                                    ? { color: "#ff3b57" }
                                    : null
                                }
                              >
                                <Grid item>
                                  <Typography
                                    variant="body2"
                                    style={{
                                      fontWeight: "bold",
                                      textTransform: "capitalize",
                                    }}
                                  >
                                    {e.song}
                                  </Typography>
                                </Grid>
                                <Grid item>
                                  <Typography variant="subtitle2">
                                    {e.infoObj.artist}
                                  </Typography>
                                </Grid>
                              </Grid>
                            </Grid>
                          </ListItem>
                          <Divider />
                        </>
                      );
                    }
                  })}
                </div>
                <div>
                  {object.songs.map((e, index) => {
                    if (index === 9) {
                      return (
                        <>
                          <ListItem
                            button
                            onClick={() => {
                              object.setSong(index);
                              this.props.checkforlike(index);
                              this.props.resetSong();
                              object.setPlayTrue();
                            }}
                          >
                            <Grid container spacing={2} justify="space-between">
                              <Grid item>
                                <img src={e.img} />
                              </Grid>
                              <Grid
                                item
                                xs
                                container
                                direction="column"
                                style={
                                  object.activeSong == index
                                    ? { color: "#ff3b57" }
                                    : null
                                }
                              >
                                <Grid item>
                                  <Typography
                                    variant="body2"
                                    style={{
                                      fontWeight: "bold",
                                      textTransform: "capitalize",
                                    }}
                                  >
                                    {e.song}
                                  </Typography>
                                </Grid>
                                <Grid item>
                                  <Typography variant="subtitle2">
                                    {e.infoObj.artist}
                                  </Typography>
                                </Grid>
                              </Grid>
                            </Grid>
                          </ListItem>
                          <Divider />
                        </>
                      );
                    }
                  })}
                </div>
              </SwipabeleViews>
            </>
          );
        }}
      </SongsContext.Consumer>
    );
  }
}

export default Playlist;
