import React, { Component } from "react";
import {
  Slider,
  Button,
  Fab,
  Typography,
  Paper,
  Grid,
  Snackbar,
  Drawer,
  Checkbox,
  List,
  ListItem,
  ListItemText,
  ClickAwayListener,
  Dialog,
  DialogContent,
  Slide,
} from "@material-ui/core";
import { SongsContext, Context } from "./context";
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import PauseIcon from "@material-ui/icons/Pause";
import SkipPreviousIcon from "@material-ui/icons/SkipPrevious";
import SkipNextIcon from "@material-ui/icons/SkipNext";
import RepeatIcon from "@material-ui/icons/Repeat";
import RepeatOneIcon from "@material-ui/icons/RepeatOne";
import Favorite from "@material-ui/icons/Favorite";
import VolumeUpIcon from "@material-ui/icons/VolumeUp";
import FavoriteBorder from "@material-ui/icons/FavoriteBorder";
import VolumeOffIcon from "@material-ui/icons/VolumeOff";
import VolumeDownIcon from "@material-ui/icons/VolumeDown";
import ShuffleIcon from "@material-ui/icons/Shuffle";
import "./css/home.css";
import Skeleton from "@material-ui/lab/Skeleton";

const obj = null;
class Home extends Component {
  state = {
    volume: 60,
    snackBarMessage: "",
    obj: null,
    play: false,
    songPosition: 0,
    img: null,
    shuffle: false,
    repeatOne: false,
    snackBarOpen: false,
    slideOpen: false,
    loading: false,

    isLiked: false,
  };
  constructor(props) {
    super(props);
    this.audioref = new React.createRef();
  }

  //to handle the volume
  handleVolume = (e, newValue) => {
    this.audioref.current.volume = newValue / 100;
    this.setState({ volume: newValue });
  };

  //to handle the play/pause of the song
  playPause = () => {
    if (this.state.play) {
      this.audioref.current.pause();
    } else {
      this.audioref.current.play();
    }
    this.setState({ play: !this.state.play });
  };

  //call back when the music player slide has changed and to set the song time to the changed value
  handleSongPosition = (e, newValue) => {
    this.audioref.current.currentTime =
      this.audioref.current.duration * (newValue / 100);
    this.setState({ songPosition: newValue });
  };

  //change the music player  slide as song plays
  changeSlide = () => {
    setInterval(() => {
      var x =
        this.audioref.current.currentTime *
        (100 / this.audioref.current.duration);
      this.setState({ songPosition: x });
    }, 1000);
  };

  //action to be taken after song ended
  songEnded = () => {};

  //repeat the song again
  repeatSong = () => {
    this.audioref.current.play();
    this.setState({ play: true });
  };

  //play the previous song
  resetSong = () => {
    this.setState({ play: true });
    this.setState({ songPosition: 0 });
    this.audioref.current.currentTime = 0;
    setTimeout(() => {
      this.audioref.current.play();
    }, 1000);
  };

  //like and unlike song
  likeUnlike = (index) => {
    if (this.state.isLiked) {
      localStorage.removeItem(index);
    } else {
      localStorage.setItem(index, index);
    }
  };

  //set like to normal on prev and next song
  checkForLike = (action, index) => {
    if (action == "prev") {
      index = index - 1;
      if (index == -1) {
        index = 9; //total lngth of list -1  ie last element
      }
    } else if (action == "random") {
      index = index;
    } else {
      index = index + 1;
      if (index == 10) {
        //10 is the length of the list
        index = 0;
      }
    }
    var x = localStorage.getItem(index);
    if (x != null) {
      this.setState({ isLiked: true });
    } else {
      this.setState({ isLiked: false });
    }
  };
  render() {
    return (
      <>
        <SongsContext.Consumer>
          {(object) => {
            return (
              <>
                <audio
                  ref={this.audioref}
                  src={require(`./music/${
                    object.songs[object.activeSong].song
                  }.mp3`)}
                  type="audio/mp3"
                  onEnded={() => {
                    this.setState({ songPosition: 0 });
                    this.audioref.current.currentTime = 0;
                    this.setState({ play: false });
                    if (this.state.repeatOne) {
                      this.repeatSong();
                    } else if (this.state.shuffle) {
                      var random = Math.floor(Math.random() * 10);
                      object.setSong(random);
                      this.checkForLike("random", random);
                      this.resetSong();
                    } else {
                      this.resetSong();
                      object.playNext();
                      this.checkForLike("next", object.activeSong);
                    }
                  }}
                  onPlaying={this.changeSlide}
                ></audio>

                <Paper
                  className="music-player-paper"
                  elevation={3}
                  style={{ position: "relative" }}
                >
                  <Typography
                    variant="body1"
                    align="center"
                    style={{ fontWeight: "bold" }}
                    className="album-title"
                  >
                    {object.songs[object.activeSong].infoObj.title}
                  </Typography>
                  <Typography
                    variant="caption"
                    align="left"
                    className="album-artist"
                  >
                    {object.songs[object.activeSong].infoObj.artist}
                  </Typography>
                  <div className="album-image">
                    {this.state.loading ? (
                      <Skeleton variant="circle" className="skeleton" />
                    ) : (
                      <img
                        src={object.songs[object.activeSong].img}
                        style={{ width: "100%", height: "100%" }}
                      />
                    )}
                  </div>
                  <div className="music-slider">
                    <Slider
                      onChange={this.handleSongPosition}
                      value={this.state.songPosition}
                      style={{ width: "100%" }}
                      className="music-slider"
                    />
                  </div>
                  <Grid container justify="center">
                    <Grid item xs={2}>
                      <Fab
                        onClick={() => {
                          this.resetSong();
                          object.playPrev();
                          this.checkForLike("prev", object.activeSong);
                        }}
                        style={{
                          color: "black",
                          backgroundColor: "transparent",
                          boxShadow: "none",
                        }}
                        size="small"
                      >
                        <SkipPreviousIcon size="small" />
                      </Fab>
                    </Grid>
                    <Grid item xs={2}>
                      <Fab
                        onClick={() => {
                          this.playPause();
                          object.handlePlayPause();
                        }}
                        style={{ color: "white", backgroundColor: "black" }}
                        size="small"
                      >
                        {this.state.play ? (
                          <PauseIcon size="small" />
                        ) : (
                          <PlayArrowIcon size="small" />
                        )}
                      </Fab>
                    </Grid>
                    <Grid item xs={2}>
                      <Fab
                        onClick={() => {
                          this.resetSong();
                          object.playNext();
                          this.checkForLike("next", object.activeSong);
                        }}
                        style={{
                          color: "black",
                          backgroundColor: "transparent",
                          boxShadow: "none",
                        }}
                        size="small"
                      >
                        <SkipNextIcon size="small" />
                      </Fab>
                    </Grid>
                  </Grid>
                  <Grid
                    container
                    justify="center"
                    alignItems="center"
                    className="bottom-nav"
                  >
                    <Grid item xs>
                      <Fab
                        style={{
                          color: "black",
                          backgroundColor: "transparent",
                          boxShadow: "none",
                        }}
                        disableRipple
                      >
                        <Checkbox
                          icon={<RepeatIcon size="small" disableRipple />}
                          checkedIcon={
                            <ShuffleIcon size="small" disableRipple />
                          }
                          size="small"
                          checked={this.state.shuffle}
                          onChange={() => {
                            var x = this.state.shuffle;
                            if (x) {
                              this.setState({
                                snackBarMessage: "Repeat All",
                              });
                            } else {
                              this.setState({
                                snackBarMessage: "Shuffle on",
                              });
                            }
                            this.setState({
                              shuffle: !x,
                              snackBarOpen: true,
                            });
                          }}
                        />
                      </Fab>
                    </Grid>
                    <Grid item xs>
                      <Fab
                        style={{
                          color: "black",
                          backgroundColor: "transparent",
                          boxShadow: "none",
                        }}
                        disableRipple
                      >
                        <Checkbox
                          icon={<FavoriteBorder size="small" disableRipple />}
                          checkedIcon={<Favorite size="small" dissbleRipple />}
                          size="small"
                          checked={this.state.isLiked}
                          onChange={() => {
                            this.likeUnlike(object.activeSong);
                            if (!this.state.isLiked) {
                              this.setState({
                                snackBarOpen: true,
                                snackBarMessage: "Added to favourites",
                              });
                            }
                            this.setState({ isLiked: !this.state.isLiked });
                          }}
                        />
                      </Fab>
                    </Grid>
                    <Grid item xs>
                      <ClickAwayListener
                        onClickAway={() => {
                          this.setState({ slideOpen: false });
                        }}
                      >
                        <Fab
                          style={{
                            color: "black",
                            backgroundColor: "transparent",
                            boxShadow: "none",
                          }}
                          disableRipple
                          onClick={() => {
                            this.setState({
                              slideOpen: !this.state.slideOpen,
                            });
                          }}
                        >
                          {this.state.volume == 0 ? (
                            <VolumeOffIcon size="small" />
                          ) : (
                            <VolumeUpIcon size="small" />
                          )}
                        </Fab>
                      </ClickAwayListener>
                    </Grid>
                    <Grid item xs>
                      <Fab
                        style={{
                          color: "black",
                          backgroundColor: "transparent",
                          boxShadow: "none",
                        }}
                        disableRipple
                      >
                        <Checkbox
                          icon={<RepeatOneIcon size="small" />}
                          checkedIcon={
                            <RepeatOneIcon
                              size="small"
                              style={{ color: "red" }}
                            />
                          }
                          size="small"
                          checked={this.state.repeatOne}
                          onChange={() => {
                            var x = this.state.repeatOne;
                            if (x) {
                              this.setState({
                                snackBarMessage: "Single repeat is off",
                              });
                            } else {
                              this.setState({
                                snackBarMessage: "Single repeat",
                              });
                            }
                            this.setState({
                              repeatOne: !x,
                              snackBarOpen: true,
                            });
                          }}
                        />
                      </Fab>
                    </Grid>
                  </Grid>
                  <Snackbar
                    open={this.state.snackBarOpen}
                    message={this.state.snackBarMessage}
                    anchorOrigin={{
                      vertical: "bottom",
                      horizontal: "left",
                    }}
                    autoHideDuration={1500}
                    onClose={() => {
                      this.setState({ snackBarOpen: false });
                    }}
                  />

                  <Slide
                    direction="up"
                    in={this.state.slideOpen}
                    style={{ position: "absolute", bottom: 0, left: 0 }}
                    timeout={{
                      enter: 600,
                      exit: 1000,
                    }}
                  >
                    <Paper
                      className="volume-slider-paper"
                      style={{ background: "whiteSmoke" }}
                    >
                      <div className="volume-slider">
                        <div>
                          {this.state.volume == 0 ? (
                            <VolumeOffIcon size="small" />
                          ) : (
                            <VolumeDownIcon size="small" />
                          )}
                        </div>
                        <Slider
                          style={{ width: "60%" }}
                          onChange={this.handleVolume}
                          defaultValue={60}
                          onMouseUp={() => {
                            this.setState({ slideOpen: false });
                          }}
                        />
                        <div>
                          <VolumeUpIcon size="small" />
                        </div>
                      </div>
                    </Paper>
                  </Slide>
                </Paper>

                <Button onClick={this.handleDialog} color="primary">
                  Info
                </Button>
              </>
            );
          }}
        </SongsContext.Consumer>
      </>
    );
  }
}

export default Home;
