import React, { Component } from "react";
import { SongsContext } from "./context";
import {
  List,
  ListItem,
  ListItemText,
  Grid,
  Typography,
  Divider,
} from "@material-ui/core";
import FlipMove from "react-flip-move";

class Favourite extends Component {
  state = {};
  render() {
    return (
      <SongsContext.Consumer>
        {(object) => {
          return (
            <List>
              {object.songs.map((e, index) => {
                var x = localStorage.getItem(index);
                if (x != null) {
                  return (
                    <>
                      <ListItem
                        button
                        onClick={() => {
                          object.setSong(index);
                          this.props.checkforlike(index);
                          this.props.resetSong();
                          object.setPlayTrue();
                        }}
                      >
                        <Grid container spacing={2} justify="space-between">
                          <Grid item>
                            <img src={e.img} />
                          </Grid>
                          <Grid
                            item
                            xs
                            container
                            direction="column"
                            style={
                              object.activeSong == index
                                ? { color: "#ff3b57" }
                                : null
                            }
                          >
                            <Grid item>
                              <Typography
                                variant="body2"
                                style={{
                                  fontWeight: "bold",
                                  textTransform: "capitalize",
                                }}
                              >
                                {e.song}
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Typography
                                variant="subtitle2"
                                style={{ overflowX: "hidden" }}
                              >
                                {e.infoObj.artist}
                              </Typography>
                            </Grid>
                          </Grid>
                        </Grid>
                      </ListItem>
                      <Divider />
                    </>
                  );
                }
              })}
            </List>
          );
        }}
      </SongsContext.Consumer>
    );
  }
}

export default Favourite;
